.newcollection__container{
    max-width: 1110px;
    margin-top: 130px;
    margin: 0 auto;
}
.newcollection__title{
    font-size: 40px;
    line-height: 44px;
    font-weight: 500;
}
.newcollection__images-block{
    margin-top: 92px;
    display: flex;
    justify-content: space-between;
}
.images__link{
    text-decoration: none;
}
.item__description-block{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #000000;
}
.item__description{
    margin-bottom: 0px;
    font-size: 20px;
    line-height: 28px;
    font-weight: 500;
}
.item__price{
    margin-top: 7px;
    font-size: 15px;
    line-height: 21px;
    font-weight: 500;
}
.newcollection__button-block{
    display: flex;
    justify-content: center;
    margin-top: 65px;
}
.newcollection__button{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 17px;
    line-height: 24px;
    font-weight: 400;
    border: none;
    width: 243px;
    height: 68px;
    border: 1px solid #6E9C9F;
    color: #6E9C9F;
    background-color: #FFFFFF;
}
.newcollection__button:hover{
    background-color: #6E9C9F;
    color: #FFFFFF;
    transition: all .4s;
}